
.product-cards__list {
  margin: 0;
  padding: 0;
  list-style-type: none;


  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 141px 60px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 143px 80px;
  }
}

.product-cards__item {
  padding: 19px 0 25px;
  border-top: 1px solid var(--special-deep-color-grey-bright);

  &:last-child {
    border-bottom: 1px solid var(--special-deep-color-grey-bright);
  }

  @media (min-width: $tablet-width) {
    padding: 0 36px 39px;
    border: none;
  }

  @media (min-width: $desktop-width) {
    padding: 0 30px 40px 35px;
  }
}
